.app-landing-page {
  position:relative;
  float: right;
  right: 50%;
  text-align: center;
}

.app-landing-page img {
  position: relative;
  right: -50%;
  height: 100vh;
  z-index: 0;
}
.app-landing-page .landing-page-text {
  position: absolute;
  top: 50%;
  left: 50%;
  right: -50%;
  height: 100vh;
  z-index: 1;
  text-align: center;
}

.app-landing-page .landing-page-date {
  font-size: 7.3rem;
  font-family: 'yg-jalnan';
  margin-bottom: -3%;
  color: rgba(255, 209, 56 ,1.0);
  text-shadow: 5px 5px rgba(0,0,0,0.5);
}

.app-landing-page .landing-page-invite-text {
  top:-10%;
  font-size: 1.3rem;
  color: white;
  text-shadow: 1.5px 1.5px rgba(0,0,0,0.5);
  padding-bottom:10px;
}

.landing-page-invite-datetime,
.landing-page-invite-location
 {
  font-size: 1.4rem;
  background-color: rgba(255, 209, 56 ,1.0);
  background-size: 20px;
}
