.summary-page {
  position: relative;
  text-align: center;
}

.summary-page img {
  max-width: 400px;
}

.summary-page-direction {
  position: absolute;
  top: 6.7%;
  left: 38%;
  text-align: center;
  font-family: 'LeeSeoyun';
  font-size: 2.0rem;
  color: black;
}

.summary-page-dir {
  font-size: 1.3rem;
}

.summary-page-dir-key {
  font-weight: 700;
}

.summary-page-dir-value {
  padding-bottom: 40px;
  font-weight: 300;
}

.summary-page-dir-comment {
  font-size: 1.0rem;
  text-decoration: rgba(139,191,210,0.8) wavy underline;
}
