.app-navbar {
  position:fixed;
  top: 0;
  margin: auto;
  max-width: 480px;
  z-index: 99999;
  font-size: 1.1rem;
}

.app-navbar ul {
  list-style:none;
}

.app-navbar ul li{
  display:inline-block;
  padding:0 7px;
  position:relative;
  margin: 5px 20px;
  padding: 5px 20px;
}

.app-navbar ul li:not(:last-child)::after{
  content:"";
  border:2px solid #000000;
  border-width: 2px 2px 0 0;
  position:absolute;
  right:-20px;
  top:0;
  height:100%;
}

.app-navbar a {
  color: #000000;
  background-size: 200px 200px;
  background-color: rgba(255,255,255,0.5) !important;
}

.app-navbar a:link {
  text-decoration: none;
  color:inherit;
  background-color: rgba(255,255,255,0.5) !important;
}
.app-navbar a:visited {
  text-decoration: none;
  color:inherit;
  background-color: rgba(255,255,255,0.5) !important;
}
.app-navbar a:hover {
  text-decoration: none;
  color: inherit;
  background-color: rgba(255, 209, 56 ,1.0) !important;
}
.app-navbar a:active {
  text-decoration: none;
  color:inherit;
  background-color: rgba(255,255,255,0.5) !important;
}
